"use client";
// React and Next
import { useEffect, useState } from "react";
import Link from "next/link";

// API
import { wishiListTotalCountApi } from "@/api/serversideaction/cartapi";
import {
  OrderListCountApi,
  QuotationListCountApi,
} from "@/api/clientsideaction/getfilter";

// Utils
import { GetAddonList } from "@/utilities/localStorage";

// 3rd Party
// Redux
import { useDispatch, useSelector } from "react-redux";
import { accountsettings } from "@/redux/slices/userSlice";

// Components
import Breadcrumb from "../Breadcrumbs";
import { useTranslation } from "react-i18next";

export default function AccountSettings() {
  const { t } = useTranslation();
  const active = useSelector((s) => s.userReducer.settingactive);
  const addonList = GetAddonList();
  const dispatch = useDispatch();
  const [wishListCount, setWishListCount] = useState([]);
  const [orderCount, setOrderCount] = useState(0);
  const [quotationCount, setQuotationCount] = useState(0);

  const handleSettings = (data) => {
    dispatch(accountsettings(data));
  };
  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "javascript:void(0)",
    },
  ];

  useEffect(() => {
    wishiListTotalCountApi(setWishListCount, dispatch);
    OrderListCountApi("opened", setOrderCount, "");
    if (
      addonList?.["product-quotation"] &&
      addonList?.["product-quotation"]?.status == 1
    ) {
      QuotationListCountApi(setQuotationCount);
    }
  }, []);

  return (
    <main className="mx-auto px-4 py-2.5 max-w-screen-2xl min-h-screen">
      <Breadcrumb breadCrumb={breadCrumb} />
      <div className="px-0 sm:px-10 lg:px-[120px] py-14 w-full">
        <div className="flex flex-col space-y-1 mb-14">
          <h4 className="text-2xl font-black text-black-300">
            {t("accountsettings.Account")}
          </h4>
          <p className="text-base font-normal text-black-300">
            {t("accountsettings.AccountSecurity")}
          </p>
        </div>
        <div className="grid grid-cols-1 gap-x-7 gap-y-4 sm:grid-cols-2 md:grid-cols-3">
          <Link
            prefetch={false}
            href="/account-settings/my-profile"
            onClick={() => handleSettings("myprofile")}
            className={` border rounded-md px-6 py-6 hover:border-black hover:drop-shadow-md hover:bg-slate-50 ${
              active == "myprofile"
                ? "border-black border drop-shadow-md bg-slate-50"
                : "border-lightgrey bg-white"
            }`}
          >
            <div className="flex items-center space-x-2.5 rtl:space-x-[unset] mb-3">
              <img
                src="/img/accnt-settings5.svg"
                alt="accnt-settings5"
                className="rtl:ml-2.5"
              />
              <h5 className="text-base font-medium leading-5 text-boldblack">
                {t("accountsettings.MyProfile")}
              </h5>
            </div>
            <p className="text-xs font-normal text-boldblack">
              {t("accountsettings.ProfileContent")}
            </p>
          </Link>
          <Link
            prefetch={false}
            href="/account-settings/order-history"
            onClick={() => handleSettings("myorders")}
            className={` border  rounded-md px-6 py-6 hover:border-black hover:drop-shadow-md hover:bg-slate-50 ${
              active == "myorders"
                ? "border-black border drop-shadow-md bg-slate-50"
                : "border-lightgrey bg-white"
            }`}
          >
            <div className="flex items-center space-x-2.5 rtl:space-x-[unset] mb-3">
              <img
                src="/img/accnt-settings1.svg"
                alt="accnt-settings1"
                className="rtl:ml-2.5"
              />
              <h5 className="text-base font-medium leading-5 text-boldblack">
                {t("accountsettings.MyOrders")}
                {orderCount !== 0 && (
                  <span className="ml-1 font-bold text-link">
                    {" "}
                    ({orderCount})
                  </span>
                )}
              </h5>
            </div>
            <p className="text-xs font-normal text-boldblack">
              {t("accountsettings.OrderContent")}
            </p>
          </Link>

          {/* My Downloads */}
          <Link
            prefetch={false}
            href="/account-settings/my-downloads"
            onClick={() => handleSettings("mydownloads")}
            className={` border  rounded-md px-6 py-6 hover:border-black hover:drop-shadow-md hover:bg-slate-50 ${
              active == "mydownloads"
                ? "border-black border drop-shadow-md bg-slate-50"
                : "border-lightgrey bg-white"
            }`}
          >
            <div className="flex items-center space-x-2.5 rtl:space-x-[unset] mb-3">
              <img
                src="/img/accnt-settings1.svg"
                alt="accnt-settings1"
                className="rtl:ml-2.5"
              />
              <h5 className="text-base font-medium leading-5 text-boldblack">
                {t("myprofile.MyDownloads")}
                {/* {orderCount !== 0 && (
                    <span className="ml-1 font-bold text-link">
                      {" "}
                      ({orderCount})
                    </span>
                  )} */}
              </h5>
            </div>
            <p className="text-xs font-normal text-boldblack">
              {t("accountsettings.downloadHistoryDescription")}
            </p>
          </Link>

          {addonList?.["product-quotation"] &&
            addonList?.["product-quotation"]?.status == 1 && (
              <Link
                prefetch={false}
                href="/account-settings/quotation-list"
                onClick={() => handleSettings("myrfo")}
                className={` border rounded-md px-6 py-6 hover:border-black hover:drop-shadow-md hover:bg-slate-50 ${
                  active == "myrfo"
                    ? "border-black border drop-shadow-md bg-slate-50"
                    : "border-lightgrey bg-white"
                }`}
              >
                <div className="flex items-center space-x-2.5 rtl:space-x-[unset] mb-3">
                  <img
                    src="/img/accnt-settings4.svg"
                    alt="accnt-settings4"
                    className="rtl:ml-2.5"
                  />
                  <h5 className="text-base font-medium leading-5 text-boldblack">
                    {t("accountsettings.MyQuotations")}
                    {quotationCount !== 0 && (
                      <span className="ml-1 font-bold text-link">
                        ({quotationCount})
                      </span>
                    )}
                  </h5>
                </div>
                <p className="text-xs font-normal text-boldblack">
                  {t("accountsettings.QuotationsContent")}
                </p>
              </Link>
            )}
          <Link
            prefetch={false}
            href="/wishlist"
            onClick={() => handleSettings("mywishlist")}
            className={` border rounded-md px-6 py-6 hover:border-black hover:drop-shadow-md hover:bg-slate-50 ${
              active == "mywishlist"
                ? "border-black border drop-shadow-md bg-slate-50"
                : "border-lightgrey bg-white"
            }`}
          >
            <div className="flex items-center space-x-2.5 rtl:space-x-[unset] mb-3">
              <img
                src="/img/accnt-settings3.svg"
                alt="accnt-settings3"
                className="rtl:ml-2.5"
              />
              <h5 className="text-base font-medium leading-5 text-boldblack">
                {t("accountsettings.MyWishlist")}
                {wishListCount?.length > 0 && (
                  <span className="ml-1 font-bold text-link">
                    ({wishListCount?.length})
                  </span>
                )}
              </h5>
            </div>
            <p className="text-xs font-normal text-boldblack">
              {t("accountsettings.WishlistContent")}
            </p>
          </Link>
          {addonList?.["support-ticket"] &&
            addonList?.["support-ticket"]?.status == 1 && (
              <Link
                prefetch={false}
                href="/account-settings/support"
                onClick={() => handleSettings("support")}
                className={` border rounded-md px-6 py-6 hover:border-black hover:drop-shadow-md hover:bg-slate-50 ${
                  active == "support"
                    ? "border-black border drop-shadow-md bg-slate-50"
                    : "border-lightgrey bg-white"
                }`}
              >
                <div className="flex items-center space-x-2.5 rtl:space-x-[unset] mb-3">
                  <img
                    src="/img/accnt-settings1.svg"
                    alt="accnt-settings1"
                    className="rtl:ml-2.5"
                  />
                  <h5 className="text-base font-medium leading-5 text-boldblack">
                    {t("accountsettings.Support")}
                  </h5>
                </div>
                <p className="text-xs font-normal text-boldblack">
                  {t("accountsettings.SupportContent")}
                </p>
              </Link>
            )}
        </div>
      </div>
    </main>
  );
}
